// option  , connect()()  props.store  
// option  mapStateToProps, MapDispatchToProps, mergeProps, options
// mapStateToProps, MapDispatchToProps, mergeProps   
//  props.store.getState().~~        

// mapStateToProps state   state   props 
//  state redux  state  state 
// redux store state   

// MapDispatchToProps  dispatch   dispatch 
//   props 

// mergeProps state dispatch    
// props state dispatch     ,  x 



// options  { pure = true, withRef = false}  .
// pure true     default  true,
// withRef true redux   ref  getWrappedInstance()
//      .   x  default false


export default Guestbook;
