import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router';
import ReactMarkdown from 'react-markdown';
import {Prism as SyntaxHighlighter} from 'react-syntax-highlighter'
import { materialLight  } from 'react-syntax-highlighter/dist/esm/styles/prism'
import { Button, Box, TextareaAutosize  } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { loadOneStudy, updateStudyText } from '../../../actions/StudyAction'
import gfm from 'remark-gfm'
import { makeStyles } from '@material-ui/core/styles';
import { AUTH_ERROR, LOAD_ONE_STUDY, LOAD_ONE_STUDY_ERROR, SERVER_ERROR, UPDATE_STUDY_TEXT, UPDATE_STUDY_TEXT_ERROR } from '../../../actions/type';

const useStyles = makeStyles(theme => ({
    edit : {
      display : 'flex',
      marginBottom : '20px',
      [theme.breakpoints.down('md')]: {
        display : 'flex',
        flexDirection : 'column',
        alignItems : 'center',
      }
    },
    text : {
      width : '50%',
      fontSize : '1rem',
      resize: 'none',
      boxSizing: 'border-box',
      padding : '20px',
      [theme.breakpoints.down('md')]: {
        width : '100%',    
      },
    },
    markdown : {
      width : '50%',
      padding : '0 20px',
      overflow : 'auto',
      [theme.breakpoints.down('md')]: {
        width : '100%',    
      }
    },
    buttonDiv :{
      display : 'flex',
      justifyContent : 'space-around',
      marginBottom : '16px',
    },
    btn : {
      fontSize: '1.5rem',
    }
}))



const Component = ({children, className}) => {
  return (
    <>
      {
        children[0].includes('\n') ? 
        <SyntaxHighlighter
          language={ className === undefined ? '' : className.substring(9)} //
          style={materialLight}
          wrapLongLines={true} //    default false
          children={children}
        /> 
        :
        <SyntaxHighlighter
          customStyle ={{
            height : 'auto',
            padding : '5px 5px',
          }}
          codeTagProps={{
            style : { color : '#7f5cc8'}
          }}
          style={materialLight}
          PreTag = 'span' //pre   
          language={ className === undefined ? '' : className.substring(9)} //
          children={children}
          wrapLongLines={true}
        />
      }
    </>

  )
};

function MarkdownEditor() {
    const classes = useStyles();
    const page = useParams().study;
    const [text, setText] = useState('');
    const history = useHistory();
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(loadOneStudy(page))
        .then(res => {
            if(res.type === LOAD_ONE_STUDY){
                setText(res.data.page.text);
            }
            if(res.type === LOAD_ONE_STUDY_ERROR){
                return alert(res.data.message);
            }
            if(res.type === SERVER_ERROR){
                return history.push('/error/500');
            }
        })
    }, [dispatch, page, history])

    const onChangeText = (e) => {
        setText(e.target.value);
    };
    const onClickUpdate = () => {
        dispatch(updateStudyText(page, text))
        .then(res => {
            if(res.type === UPDATE_STUDY_TEXT){
                return history.push(`/study/${page}`)
            }
            if(res.type === UPDATE_STUDY_TEXT_ERROR){
                return alert(res.data.message);
            }
            if(res.type === AUTH_ERROR){
                alert(res.data.message);
                return history.push('/login');
            }
            if(res.type === SERVER_ERROR){
                return history.push('/error/500');
            }
        })
    };
    const onClickCancel = () => {
        history.push(`/study/${page}`)
    }
    return (
      <>
        <Box className={classes.edit}>
          <TextareaAutosize  className={classes.text} variant='outlined' value={text} onChange={onChangeText} autoFocus />
          <ReactMarkdown className={classes.markdown} remarkPlugins={[gfm, {singleTilde: false}]} children={text} components= {{
            code : Component
          }}/>
        </Box>
        <Box className={classes.buttonDiv}>
          <Button className={classes.btn} variant="contained" onClick={onClickUpdate}></Button>
          <Button className={classes.btn} variant="contained" onClick={onClickCancel}></Button>
        </Box>
      </>
    )
}

export default MarkdownEditor
