import React, { useState, useEffect } from 'react'
import { Box, SvgIcon, Typography, Button, TextField } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { useHistory, useLocation } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { newPassword } from '../../../../actions/UserAction';
import { NEW_PASSWORD, NEW_PASSWORD_FAIL, SERVER_ERROR } from '../../../../actions/type';

const useStyles = makeStyles(theme => ({
    form : {
        display : 'flex',
        flexDirection : 'column'
    },
    icon : {
        fontSize : '48px',
    },
    title : {
        display : 'flex',
        flexDirection : 'column',
        alignItems :'center',
        marginBottom : '24px'
    },
    input : {
        marginBottom : '24px',
    },
    text : {
        paddingBottom : '12px'
    },
    btnArea : {
        display : 'flex',
        justifyContent : 'space-evenly'
    }
}))

const checkPassword = password => {
    const blank = /\s/
    const regex = /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$/;
    if(!blank.test(password)){
        if(regex.test(password)){
            return true
        }else{
            return false
        }
    }else{
        return false
    }
}

function Password() {
    const location = useLocation();
    const classes = useStyles();
    const [password, setPassword] = useState('')
    const [confirm, setConfirm] = useState('')
    const history = useHistory();
    const dispatch = useDispatch();

    //      
    useEffect(() => {
        if(!location.state){
            history.push('/login');
        }
    },[history, location.state])

    const onSubmitData = (e) => {
        e.preventDefault();
        if(checkPassword(password)){
            if(password !== confirm){
                return alert('  ');
            }
            dispatch(newPassword(password, location.state.userId))
            .then(res => {
                if(res.type === NEW_PASSWORD){
                    return history.push('/login')
                }
                if(res.type === NEW_PASSWORD_FAIL){
                    return alert(res.data.message);
                }
                if(res.type === SERVER_ERROR){
                    return history.push('/error/500');
                }
            })
        }else{
            return alert('      8, 16 ')
        }
    }

    const onChangePassword = (e) => {
        setPassword(e.target.value);
    }

    const onChangeConfirm = (e) => {
        setConfirm(e.target.value);
    }

    const onClickClose = () => {
        history.push('/login');
    }
    
    return (
        <Box className={classes.area}>
            <Box className={classes.title}>
                <SvgIcon className={classes.icon}><AccountCircleIcon /></SvgIcon>
                <Typography variant='h4'> </Typography>
            </Box>
            <form onSubmit={onSubmitData} className={classes.form}>
                <Box className={classes.input}>
                    <TextField type='password' onChange={onChangePassword} className={classes.text} fullWidth label='' variant='outlined' autoComplete='off' required/>
                    <TextField type='password' onChange={onChangeConfirm} fullWidth label=' ' variant='outlined' required autoComplete='off'/>
                </Box>
                <Box className={classes.btnArea}>
                    <Button type='submit' color='primary' variant='contained'></Button>
                    <Button onClick={onClickClose} variant='outlined'></Button>
                </Box>
            </form>
        </Box>
    )
}

export default Password
