import React, { useEffect } from 'react'
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { AUTH_ERROR, AUTH_USER, SERVER_ERROR } from '../actions/type';
import { authUser } from '../actions/UserAction';
function Auth(Component, option, adminRoute = null) {
    //component => hoc  
    //option => null:  ,
    //          true =>  ,
    //          false =>    
    //adminRoute => true : admin User 
    function AuthenticationCheck(){
        const dispatch = useDispatch();
        const history = useHistory();
        useEffect(() => {
            let access = localStorage.getItem('access');
            if(!access){
                access = '';
            }
            dispatch(authUser(access))
            .then(res => {
                if(res.type === AUTH_USER){
                    //   
                    if(res.token !== access){
                        localStorage.setItem('access', res.data.token);
                    }
                    //     
                    //adminpage 
                    if(adminRoute && res.data.user.role !== 3){
                        return history.push('/');
                    }
                    if(option === false){
                        return history.push('/');
                    }
                }
                if(res.type === AUTH_ERROR){
                    localStorage.removeItem('access');
                    if(res.data.expire){
                        alert(res.data.message);
                        return history.push('/login');
                    }else{
                        if(option){
                            return history.push('/login');
                        }
                    }
                }
                if(res.type === SERVER_ERROR){
                    return history.push('/error/500');
                }
            })
        }, [dispatch, history])

        return (
            <Component />
        )
    }
    return (
        AuthenticationCheck
    )
}

export default Auth
