import React from 'react'
import { Button, Box, Typography } from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { resignUser, resignOAuthUser } from '../../../../actions/UserAction';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { AUTH_ERROR, DELETE_USER, DELETE_USER_ERROR, SERVER_ERROR } from '../../../../actions/type';

const useStyles = makeStyles(theme => ({
    title : {
        margin : '16px 0',
        fontWeight : 'bold'
    },
    area : {
        display : 'flex',
        flexDirection: 'column',
        alignItems : 'center',
    },
    text : {
        fontSize : '2rem'
    },
    textArea : {
        textAlign : 'center',
        marginBottom : '32px',
    }
}))

function Resign() {
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory();
    const user = useSelector(state => state.UserReducer.user);
    const onClickBtn = () => {
        if(window.confirm(' ?')){
            if( user.provider === 'local'){
                const password = window.prompt('     .')
                dispatch(resignUser(password))
                .then(res => {
                    if(res.type === DELETE_USER){
                        alert(res.data.message);
                        localStorage.removeItem('access');
                        return history.push('/');
                    }
                    if (res.type === DELETE_USER_ERROR){
                        return alert(res.data.message);
                    }
                    if(res.type === AUTH_ERROR){
                        alert(res.data.message);
                        return history.push('/login');
                    }
                    if(res.type === SERVER_ERROR){
                        history.push('/error/500');
                    }
                }) 
            }else{
                dispatch(resignOAuthUser())
                .then(res => {
                    if(res.type === DELETE_USER){
                        alert(res.data.message);
                        return history.push('/');
                    }
                    if(res.type === DELETE_USER_ERROR){
                        return alert(res.data.message);
                    }
                    if(res.type === SERVER_ERROR){
                        return history.push('/error/500')
                    }
                })
            }
        }
    };
    return (
        <Box>
            <Typography variant='h5' className={classes.title}> </Typography>
            <Box className={classes.area}>
                <Box className={classes.textArea}>
                    <Typography className={classes.text} variant='body1'>
                           .<br />
                           .
                    </Typography>
                </Box>
                <Button variant="outlined" onClick={onClickBtn}></Button>
            </Box>
        </Box>
    )
}

export default Resign
